@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Bree+Serif&family=EB+Garamond:ital,wght@0,500;1,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:100,200,300,400,500,600,700,800,900&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: sans-serif;
}

body {
  margin: 0;
 
}
.maindata{
	box-shadow: 0 15px 30px 1px grey;
	background: rgba(255, 255, 255, 0.90);
	text-align: center;
	border-radius: 5px;
	overflow: hidden;
	margin: 5em auto;
	height: 350px;
	width: 700px;
  
	
}



.product-details {
	position: relative;
	text-align: left;
	overflow: hidden;
	padding: 30px;
	height: 100%;
	float: left;
	width: 40%;

}

.maindata .product-details h1{
	font-family: 'Bebas Neue', cursive;
	display: inline-block;
	position: relative;
	font-size: 30px;
	color: #344055;
	margin: 0;
	
}

.maindata .product-details h1:before{
	position: absolute;
	content: '';
	right: 0%; 
	top: 0%;
	-webkit-transform: translate(25px, -15px);
	        transform: translate(25px, -15px);
	font-family: 'Bree Serif', serif;
	display: inline-block;
	background: #ffe6e6;
	border-radius: 5px;
	font-size: 14px;
	padding: 5px;
	color: white;
	margin: 0;
	-webkit-animation: chan-sh 6s ease infinite;
	        animation: chan-sh 6s ease infinite;

}

.hint-star {
	display: inline-block;
	margin-left: 0.5em;
	color: gold;
	width: 50%;
}


.maindata .product-details > p {
font-family: 'EB Garamond', serif;
	text-align: center;
	font-size: 18px;
	color: #7d7d7d;
	
}
.control{
	position: absolute;
	bottom: 20%;
	left: 22.8%;
	
}
.btn {

	-webkit-transform: translateY(0px);

	        transform: translateY(0px);
	transition: 0.3s linear;
	background:  #809fff;
	border-radius: 5px;
  position: relative;
  overflow: hidden;
	cursor: pointer;
	outline: none;
	border: none;
	color: #eee;
	padding: 0;
	margin: 0;
	
}

.btn:hover{-webkit-transform: translateY(-6px);transform: translateY(-6px);
	background: #1a66ff;}

.btn span {
	font-family: 'Farsan', cursive;
	transition: -webkit-transform 0.3s;
	transition: transform 0.3s;
	transition: transform 0.3s, -webkit-transform 0.3s;
	display: inline-block;
  padding: 10px 20px;
	font-size: 1.2em;
	margin:0;
	
}
.btn .price, .shopping-cart{
	background: #333;
	border: 0;
	margin: 0;
}

.btn .price {
	-webkit-transform: translateX(-10%);
	        transform: translateX(-10%); padding-right: 15px;
}

.btn .shopping-cart {
	-webkit-transform: translateX(-100%);
	        transform: translateX(-100%);
  position: absolute;
	background: #333;
	z-index: 1;
  left: 0;
  top: 0;
}

.btn .buy {z-index: 3; font-weight: bolder;}

.btn:hover .price {-webkit-transform: translateX(-110%);transform: translateX(-110%);}

.btn:hover .shopping-cart {-webkit-transform: translateX(0%);transform: translateX(0%);}



.product-image {
	transition: all 0.3s ease-out;
	display: inline-block;
	position: relative;
	overflow: hidden;
	height: 100%;
	float: right;
	width: 45%;
	display: inline-block;
}

.maindata img {width: 100%;height: 100%;}

.info {
    background: rgba(27, 26, 26, 0.9);
    font-family: 'Bree Serif', serif;
    transition: all 0.3s ease-out;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    position: absolute;
    line-height: 1.8;
    text-align: left;
    font-size: 105%;
    cursor: no-drop;
    color: #FFF;
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
}

.info h2 {text-align: center}
.product-image:hover .info{-webkit-transform: translateX(0);transform: translateX(0);}

.info ul li{transition: 0.3s ease;}
.info ul li:hover{-webkit-transform: translateX(50px) scale(1.3);transform: translateX(50px) scale(1.3);}

.product-image:hover img {transition: all 0.3s ease-out;}
.product-image:hover img {-webkit-transform: scale(1.2, 1.2);transform: scale(1.2, 1.2);}
body {
	 background-color: #f4f7f9;
}
 .container {
	 width: 450px;
	 margin: 50px auto;
	 min-height: 200px;
	 padding: 50px;
	 text-align: center;
	 position: relative;
}
 .container h1 {
	 font-family: 'Quicksand', sans-serif;
	 margin-bottom: 40px;
}
 .container p {
	 font-family: 'Quicksand', sans-serif;
	 color: #888;
	 margin-top: 50px;
}
 .container .blank_space {
	 height: 75px;
	 display: block;
}
 .container .fieldRow {
	 position: relative;
	 display: inline-block;
	 width: 450px;
	 margin: 10px 0;
}
 .container .fieldRow.col2 {
	 width: 210px;
	 float: left;
}
 .container .fieldRow.last {
	 float: right;
}
 .container .fieldRow.focus input {
	 outline: none;
	 border-color: #4aa9f2;
	 box-shadow: 0px 0px 2px 3px #edf2f6;
	 background-color: #fff;
}
 .container .fieldRow.focus input::-webkit-input-placeholder {
	 color: #8d9dac;
}
 .container .fieldRow.focus input::-moz-input-placeholder {
	 color: #8d9dac;
}
 .container .fieldRow.focus input::-ms-input-placeholder {
	 color: #8d9dac;
}
 .container .fieldRow.focus input + label {
	 left: -100px;
	 opacity: 0;
}
 .container .fieldRow.focus input + label + i {
	 opacity: 1;
}
 .container .fieldRow.active input {
	 outline: none;
	 border-color: #d2dbe2;
	 box-shadow: none;
	 background-color: #ecf1f5;
}
 .container .fieldRow.active input::-webkit-input-placeholder {
	 color: #8d9dac;
}
 .container .fieldRow.active input::-moz-input-placeholder {
	 color: #8d9dac;
}
 .container .fieldRow.active input::-ms-input-placeholder {
	 color: #8d9dac;
}
 .container .fieldRow.active input + label {
	 left: -100px;
	 opacity: 0;
}
 .container .fieldRow.active input + label + i {
	 opacity: 1;
}
 .container .fieldRow.active .iconCheck {
	 color: #98a7b4 !important;
}
 .container .fieldRow.credit_card {
	 transition: all 0.35s ease;
}
 .container .fieldRow.credit_card.zoomOn {
	 -webkit-transform: scale(2);
	         transform: scale(2);
	 z-index: 999;
}
 .container .fieldRow.credit_card.zoomOn .zoomBtn {
	 display: none;
}
 .container .fieldRow.credit_card.zoomOn .closeBtn {
	 display: block;
}
 .container input {
	 display: inline-block;
	 padding: 8px 50px;
	 border-radius: 4px;
	 border: solid 2px #d2dbe2;
	 height: 55px;
	 box-sizing: border-box;
	 width: 100%;
	 font-family: 'Quicksand', sans-serif;
	 font-size: 18px;
	 transition: all 0.3s ease-in-out;
	 color: #000;
}
 .container input.credit_card_number {
	 padding-right: 100px;
}
 .container input + label {
	 position: absolute;
	 display: inline-block;
	 top: 15px;
	 left: 20px;
	 color: #8d9dac;
	 font-family: 'Quicksand', sans-serif;
	 font-size: 14px;
	 font-size: 18px;
	 opacity: 1;
	 transition: all 0.6s ease;
}
 .container input::-webkit-input-placeholder {
	 color: #fff;
}
 .container input::-moz-input-placeholder {
	 color: #fff;
}
 .container input::-ms-input-placeholder {
	 color: #fff;
}
 .container i {
	 position: absolute;
	 left: 20px;
	 top: 17px;
	 opacity: 0;
	 color: #8d9dac;
	 font-size: 21px;
}
 .container #iconCC {
	 position: static;
	 position: initial;
	 left: 0;
	 opacity: 1;
	 color: #999;
	 width: 30px;
	 display: inline-block;
}
 .container .closeBtn {
	 position: absolute;
	 top: 11px;
	 right: 10px;
	 padding: 8px 10px;
	 background-color: #d2dbe2;
	 color: #888;
	 border-radius: 4px;
	 font-size: 14px;
	 transition: all 0.7s ease;
	 display: none;
}
 .container .closeBtn i {
	 position: relative;
	 left: 0;
	 top: 0;
	 color: #333;
	 opacity: 1;
	 font-size: 18px;
}
 .container .closeBtn:hover {
	 background-color: #888;
	 color: #fff;
	 cursor: pointer;
}
 .container .zoomBtn {
	 position: absolute;
	 top: 11px;
	 right: 10px;
	 padding: 8px 10px;
	 background-color: #d2dbe2;
	 color: #888;
	 border-radius: 4px;
	 font-family: 'Quicksand', sans-serif;
	 font-size: 14px;
	 transition: all 0.7s ease;
}
 .container .zoomBtn:hover {
	 background-color: #888;
	 color: #fff;
	 cursor: pointer;
}
 .container .zoomBtn:hover:after, .container .zoomBtn:hover:before {
	 opacity: 1;
	 visibility: visible;
	 -webkit-transform: translateY(0);
	         transform: translateY(0);
}
 .container .zoomBtn:before {
	 content: 'Enlarge the number to better control';
	 width: 300px;
	 height: 20px;
	 padding: 5px 0;
	 background-color: #888;
	 display: block;
	 position: absolute;
	 z-index: 1;
	 top: -50px;
	 visibility: hidden;
	 right: 0px;
	 border-radius: 4px;
	 color: #fff;
	 opacity: 0;
	 transition: all 0.7s ease 0.2s;
	 -webkit-transform: translateY(50px);
	         transform: translateY(50px);
}
 .container .zoomBtn:after {
	 width: 0;
	 height: 0;
	 border-style: solid;
	 border-width: 15px 20px 0 20px;
	 border-color: #888 transparent transparent transparent;
	 content: '';
	 top: -21px;
	 right: 12px;
	 position: absolute;
	 visibility: hidden;
	 opacity: 0;
	 transition: all 0.7s ease 0.2s;
	 -webkit-transform: translateY(50px);
	         transform: translateY(50px);
}
 .container .iconCheck {
	 position: absolute;
	 right: 20px;
	 left: auto;
	 top: 17px;
	 opacity: 1;
	 display: none;
}
 .container #iconWrong {
	 color: #ea4949;
}
 .container #iconPassed {
	 color: #73ba4b;
}
 button[type="submit"] {
	 width: 450px;
	 margin: 20px 0;
	 background: transparent;
	 border: none;
	 background: #4aa9f2;
	 color: #fff;
	 padding: 15px;
	 border-radius: 4px;
	 font-family: 'Quicksand', sans-serif;
	 font-size: 18px;
	 transition: all 0.35s ease;
}
 button[type="submit"]:hover {
	 cursor: pointer;
	 background: #87d37c;
}
 .overlay {
	 position: fixed;
	 background: #fff;
	 width: 100%;
	 height: 100%;
	 top: 0;
	 left: 0;
	 bottom: 0;
	 margin: auto;
	 z-index: 998;
	 opacity: 0.6;
	 display: none;
}
 .overlay.open {
	 display: block;
}
 
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

section {
  position: relative;
  min-height: 100vh;
  background-color: #f8dd30;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

section .container {
  position: relative;
  width: 800px;
  height: 500px;
  background: #fff;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

section .container .user {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
}

section .container .user .imgBx {
  position: relative;
  width: 50%;
  height: 100%;
  background: #ff0;
  transition: 0.5s;
}

section .container .user .imgBx img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

section .container .user .formBx {
  position: relative;
  width: 50%;
  height: 100%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 40px;
  transition: 0.5s;
}

section .container .user .formBx form h2 {
  font-size: 18px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-align: center;
  width: 100%;
  margin-bottom: 10px;
  color: #555;
}

section .container .user .formBx form input {
  position: relative;
  width: 100%;
  padding: 10px;
  background: #f5f5f5;
  color: #333;
  border: none;
  outline: none;
  box-shadow: none;
  margin: 8px 0;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 300;
}

section .container .user .formBx form input[type='submit'] {
  max-width: 100px;
  background: #677eff;
  color: #fff;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  transition: 0.5s;
}

section .container .user .formBx form .signup {
  position: relative;
  margin-top: 20px;
  font-size: 12px;
  letter-spacing: 1px;
  color: #555;
  text-transform: uppercase;
  font-weight: 300;
}

section .container .user .formBx form .signup a {
  font-weight: 600;
  text-decoration: none;
  color: #677eff;
}

section .container .signupBx {
  pointer-events: none;
}

section .container.active .signupBx {
  pointer-events: initial;
}

section .container .signupBx .formBx {
  left: 100%;
}

section .container.active .signupBx .formBx {
  left: 0;
}

section .container .signupBx .imgBx {
  left: -100%;
}

section .container.active .signupBx .imgBx {
  left: 0%;
}

section .container .signinBx .formBx {
  left: 0%;
}

section .container.active .signinBx .formBx {
  left: 100%;
}

section .container .signinBx .imgBx {
  left: 0%;
}

section .container.active .signinBx .imgBx {
  left: -100%;
}

@media (max-width: 991px) {
  section .container {
    max-width: 400px;
  }

  section .container .imgBx {
    display: none;
  }

  section .container .user .formBx {
    width: 100%;
  }
}

